import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'myfilter'

})

export class MyFilterPipe implements PipeTransform {
city: any;

  transform(statecities: any, selected: any): any {
        if (selected === undefined) {
            return statecities;
        }

        return statecities.filter(function(city) {
          city.stateid.toLowerCase().includes(selected.stateid.toLowerCase());
         });
        }
      }



