import { Component, OnInit } from '@angular/core';
import { ServicesComponent } from '../services.component';
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';
import { catchError, retry  } from 'rxjs/operators';


@Component({
  selector: 'app-factype',
  templateUrl: './factype.component.html',
  styleUrls: ['../app.component.css']
})
export class FactypeComponent implements OnInit {

  constructor(private services: ServicesComponent ) { }
  nAdded = 0;
  NewFacility = 0;
  btnNewFacility = false;
  btnUpdate = true;
  nUpdate = 0;
ufacility: any;
  fmodel= {
    roleSearch: '',
  rolesearchparam: ''
  };
mFacilitylist: any;
errormsg: any;
  nuSers: any;
fmole = {
  facilityid: '',
  facilitydesc: '',
  facilitytype: '',
  rate: 0,
ratetype: '',
  minrate: 0,
managefee: 0,
  maxlimit: 0,
  limitoverrideby: ''
};


setNewFacility() {
  this.btnNewFacility = true;
this.NewFacility = 2;
}

frefresh() {
  this.NewFacility = 0;
  this.fmole.facilityid = '';
    this.fmole.facilitydesc = '';
    this.fmole.facilitytype  = '';
    this.fmole.rate = 0;
    this.fmole.ratetype = '';
    this.fmole.managefee = 0;
    this.fmole.minrate = 0;
    this.fmole.maxlimit = 0;
    this.fmole.limitoverrideby = '';
  this.getAllFacility(null);
}

deleteFacility(nFacilityID: any)   {
  // return this.http.get(this.UserRoleUrl).subscribe(data => console.log(data));
     // this._mRole = JSON.stringify(nrole);
     if (confirm('Are you sure you want to delete this record?') === true) {
      this.NewFacility = 0;
    // tslint:disable-next-line:max-line-length
    return this.services.deleteFacility(nFacilityID).subscribe(resp => this.getDeleteStatus(resp), error => this.services.handleError(error));
  }
  }

  getAllFacility(searchP: any) {

    this.NewFacility = 0;
    if (searchP == null) {
     // tslint:disable-next-line:max-line-length
     return this.services.getFacility(searchP).subscribe(sfacility => this.mFacilitylist = sfacility, error => this.services.handleError(error));
    }
    // tslint:disable-next-line:one-line
    else  {

    // tslint:disable-next-line:max-line-length
    return this.services.getFacility(searchP).subscribe(sfacility => this.mFacilitylist = sfacility, error => this.services.handleError(error));
  }
  }


  getFacility(nFacilityID: any)   {
    // return this.http.get(this.UserRoleUrl).subscribe(data => console.log(data));
  /* this.model.rolesearchparam = 'roleid';
  this.model.roleSearch = nRoleID;
   */  if (nFacilityID == null)
    // tslint:disable-next-line:one-line
    {
     alert('Invalid Facility ID');
    }   else {

    this.fmole.facilityid = nFacilityID.facilityid;
    this.fmole.facilitydesc = nFacilityID.facilitydesc;
    this.fmole.facilitytype  = nFacilityID.facilitytype;
    this.fmole.ratetype = nFacilityID.ratetype;
    this.fmole.rate = nFacilityID.rate;
    this.fmole.minrate = nFacilityID.minrate;
    this.fmole.managefee = nFacilityID.managefee;
    this.fmole.maxlimit = nFacilityID.maxlimit;
    this.fmole.limitoverrideby = nFacilityID.limitoverrideby;
this.btnNewFacility = false;
    this.btnUpdate = true;
    this.NewFacility = 2;
    }
  }

 AddNewFacility(ufacility: any) {
this.NewFacility = 2;
this.ufacility = JSON.stringify(ufacility);
if ( this.btnNewFacility === true ) {
  this.services.addNewFacility( ufacility ).subscribe(resp => this.getStatus(resp), error => this.services.handleError(error));
  this.NewFacility = 2;
  this.btnNewFacility = false;
  } else if (this.btnUpdate === true) { // Update
    this.services.updateFacility( ufacility ).subscribe(resp => this.getStatus(resp), error => this.services.handleError(error) );
    this.fmole.facilityid = ufacility.facilityid;
    this.fmole.facilitydesc = ufacility.facilitydesc;
    this.fmole.facilitytype  = ufacility.facilitytype;
    this.fmole.ratetype = ufacility.ratetype;
    this.fmole.rate = ufacility.rate;
    this.fmole.minrate = ufacility.minrate;
    this.fmole.managefee = ufacility.managefee;
    this.fmole.maxlimit = ufacility.maxlimit;
    this.fmole.limitoverrideby = ufacility.limitoverrideby;

    this.NewFacility = 2;
    this.btnUpdate = false;
  }
  }

  getStatus(numUpdate: any) {
    if (numUpdate === 1 ) {
      alert('Operation Successful');
      } else {
        alert('Unable to Update Record at this time; Please Try Again Later');
      }

  }

  getDeleteStatus(numUpdate: any) {
    if (numUpdate === 1 ) {
      this.getAllFacility(null);
      alert('Operation Successful');
      } else {
        alert('Operation not successful at this time; Please Try Again Later');
      }

}

  ngOnInit() {
  }

}



