import { Component, OnInit } from '@angular/core';
import { forEach } from '@angular/router/src/utils/collection';
import { angularMath } from 'angular-ts-math';

@Component({
  selector: 'app-loanslider',
  templateUrl: './loanslider.component.html',
  styleUrls: ['../app.component.css']
})
export class LoansliderComponent implements OnInit {
iniprincipal: number;
principals: number[] = [];
monthes: number[] = [];

lslider = {
principal: 0,
rate: 10.0,
mnthrepay: 0.00,
totalrepay: 0.00,
month: 1,
princialslide: 10000,
monthslide: 1

};
monthlyInterest: number;
monthrepay: number;
principalAmt: number ;
months: number;
compprincipal: number;
totalrepayment: number;
  constructor() { }

Repayment() {
// this.monthrepay = 0;
this.compprincipal = 0;
this.totalrepayment = 0;
this.principalAmt = 0;
this.monthrepay = 0;
this.months = this.lslider.monthslide;

this.principalAmt = this.lslider.principal;


for (let t = 1; t <= this.lslider.month; t = t + 1) {
  this.monthlyInterest = ( this.principalAmt * 1 *  10) / 100;
  this.compprincipal = angularMath.sum( this.principalAmt, this.monthlyInterest);
this.monthrepay = this.monthrepay + this.principalAmt;
this.principalAmt = this.compprincipal;
  this.totalrepayment = this.totalrepayment + this.compprincipal;
}
this.lslider.mnthrepay = this.monthrepay;
this.lslider.totalrepay = this.totalrepayment;

  // alert(this.lslider.mnthrepay);
}



  ngOnInit() {
      for (let i = 50000; i <= 4000000; i = i + 10000)
    // tslint:disable-next-line:one-line
    {
      this.principals.push (i);
      }

      for (let t = 1; t <= 12; t = t + 1)
      // tslint:disable-next-line:one-line
      {
        this.monthes.push (t);
        }

    }
  }






