import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import {AppComponent  } from './app.component';
// import { NewroleComponent } from './userrole/newrole.component';
import { UserroleComponent } from './userrole/userrole.component';
import { FactypeComponent } from './factype/factype.component';
import { PostrepayComponent } from './postrepay/postrepay.component';
import { CustomerinfoComponent } from './customerinfo/customerinfo.component';
import { Mainbar2Component } from './mainbar2/mainbar2.component';
import { ApplyloanComponent } from './applyloan/applyloan.component';
import { LoansliderComponent } from './loanslider/loanslider.component';

 export const router: Routes =  [
{path: '' , redirectTo: 'userrole', pathMatch: 'full' },
//  {path: 'newrole', component: NewroleComponent },
 {path: 'facilitytype', component: FactypeComponent },
 {path: 'repaypost', component: PostrepayComponent },
 {path: 'userrole', component: UserroleComponent},
 {path: 'customer', component: CustomerinfoComponent},
 {path: 'mainheader', component: Mainbar2Component },
 {path: 'applyloan', component: ApplyloanComponent},
 {path: 'loanslier', component: LoansliderComponent}
 ];

export const routes: ModuleWithProviders = RouterModule.forRoot(router);




